.popup {
  position: absolute;
  top: 10px;
  right: 10px;
  height: min(calc(100vh - 10px), 95%);
  width: calc(60% - 25px);
  border-radius: 4px;
  background-image: linear-gradient(-70deg, rgba(46, 154, 255, .8) 60%, rgba(244, 152, 173, .7));
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 200ms, opacity 200ms;
}

@media screen and (max-width: 767px) {
  .popup {
    width: calc(100% - 15px);
    background-image: linear-gradient(-70deg, rgba(46, 154, 255, .9) 60%, rgba(244, 152, 173, .5));
  }

}
